.call-button {
    padding: 7px;
}

.call-tile {
    position: relative;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.call.document {
    align-items: center;
}

.call .document-title {
    margin-top: 0;
}
